rflow-x: hidden;\r\n}\r\n\r\n// Prevent overscroll and improve mobile experience\r\nbody {\r\n  -webkit-overflow-scrolling: touch;\r\n  -webkit-touch-callout: none;\r\n  -webkit-user-select: none;\r\n  -khtml-user-select: none;\r\n  -moz-user-select: none;\r\n  -ms-user-select: none;\r\n  user-select: none;\r\n  background-color: var(--bg-secondary);\r\n}\r\n\r\n#root {\r\n  height: 100%;\r\n  min-height: 100vh;\r\n  position: relative;\r\n  display: flex;\r\n  flex-direction: column;\r\n}\r\n\r\n.app {\r\n  height: 100%;\r\n  min-height: 100vh;\r\n  display: flex;\r\n  flex-direction: column;\r\n}\r\n\r\n// Design tokens\r\n:root {\r\n  // Colors\r\n  --color-primary: #0078d4;\r\n  --color-primary-dark: #106ebe;\r\n  --color-secondary: #6c757d;\r\n  --color-success: #107c10;\r\n  --color-warning: #ff8c00;\r\n  --color-error: #d13438;\r\n  \r\n  // Backgrounds\r\n  --bg-primary: #ffffff;\r\n  --bg-secondary: #f8f9fa;\r\n  --bg-tertiary: #e9ecef;\r\n  \r\n  // Text colors\r\n  --text-primary: #323130;\r\n  --text-secondary: #605e5c;\r\n  --text-tertiary: #8a8886;\r\n  \r\n  // Shadows\r\n  --shadow-card: 0 2px 8px rgba(0, 0, 0, 0.1);\r\n  --shadow-card-hover: 0 4px 16px rgba(0, 0, 0, 0.15);\r\n  --shadow-overlay: 0 8px 32px rgba(0, 0, 0, 0.2);\r\n  \r\n  // Border radius\r\n  --radius-small: 4px;\r\n  --radius-medium: 8px;\r\n  --radius-large: 12px;\r\n  --radius-xl: 16px;\r\n  \r\n  // Spacing\r\n  --spacing-xs: 4px;\r\n  --spacing-sm: 8px;\r\n  --spacing-md: 16px;\r\n  --spacing-lg: 24px;\r\n  --spacing-xl: 32px;\r\n  --spacing-xxl: 48px;\r\n  \r\n  // Typography\r\n  --font-size-xs: 12px;\r\n  --font-size-sm: 14px;\r\n  --font-size-base: 16px;\r\n  --font-size-lg: 18px;\r\n  --font-size-xl: 20px;\r\n  --font-size-xxl: 24px;\r\n  --font-size-xxxl: 32px;\r\n  \r\n  --font-weight-normal: 400;\r\n  --font-weight-medium: 500;\r\n  --font-weight-semibold: 600;\r\n  --font-weight-bold: 700;\r\n  \r\n  // Layout breakpoints\r\n  --breakpoint-sm: 576px;\r\n  --breakpoint-md: 768px;\r\n  --breakpoint-lg: 992px;\r\n  --breakpoint-xl: 1200px;\r\n  \r\n  // Z-index layers\r\n  --z-index-dropdown: 1000;\r\n  --z-index-sticky: 1020;\r\n  --z-index-fixed: 1030;\r\n  --z-index-modal-backdrop: 1040;\r\n  --z-index-modal: 1050;\r\n  --z-index-popover: 1060;\r\n  --z-index-tooltip: 1070;\r\n}\r\n\r\n// Utility classes\r\n.visually-hidden {\r\n  position: absolute !important;\r\n  width: 1px !important;\r\n  height: 1px !important;\r\n  padding: 0 !important;\r\n  margin: -1px !important;\r\n  overflow: hidden !important;\r\n  clip: rect(0, 0, 0, 0) !important;\r\n  white-space: nowrap !important;\r\n  border: 0 !important;\r\n}\r\n\r\n.no-scroll {\r\n  overflow: hidden;\r\n  height: 100vh;\r\n}\r\n\r\n// Touch and interaction improvements\r\nbutton, [role=\"button\"] {\r\n  touch-action: manipulation;\r\n  -webkit-tap-highlight-color: transparent;\r\n}\r\n\r\n// Focus styles for accessibility\r\n:focus-visible {\r\n  outline: 2px solid var(--color-primary);\r\n  outline-offset: 2px;\r\n}\r\n\r\n// Smooth scrolling\r\nhtml {\r\n  scroll-behavior: smooth;\r\n}\r\n\r\n// Responsive typography\r\n@media (max-width: 480px) {\r\n  :root {\r\n    --font-size-xs: 11px;\r\n    --font-size-sm: 13px;\r\n    --font-size-base: 15px;\r\n    --font-size-lg: 17px;\r\n    --font-size-xl: 19px;\r\n    --font-size-xxl: 22px;\r\n    --font-size-xxxl: 28px;\r\n  }\r\n}\r\n\r\n// High contrast mode support\r\n@media (prefers-contrast: high) {\r\n  :root {\r\n    --shadow-card: 0 2px 8px rgba(0, 0, 0, 0.3);\r\n    --shadow-card-hover: 0 4px 16px rgba(0, 0, 0, 0.4);\r\n  }\r\n}\r\n\r\n// Reduced motion support\r\n@media (prefers-reduced-motion: reduce) {\r\n  * {\r\n    animation-duration: 0.01ms !important;\r\n    animation-iteration-count: 1 !important;\r\n    transition-duration: 0.01ms !important;\r\n  }\r\n  \r\n  html {\r\n    scroll-behavior: auto;\r\n  }\r\n}\r\n\r\n// Dark theme variables\r\n[data-theme=\"dark\"] {\r\n  // Colors\r\n  --color-primary: #4fc3f7;\r\n  --color-primary-dark: #29b6f6;\r\n  --color-secondary: #90a4ae;\r\n  --color-success: #4caf50;\r\n  --color-warning: #ff9800;\r\n  --color-error: #f44336;\r\n  \r\n  // Backgrounds\r\n  --bg-primary: #121212;\r\n  --bg-secondary: #1e1e1e;\r\n  --bg-tertiary: #2d2d2d;\r\n  \r\n  // Text colors\r\n  --text-primary: #ffffff;\r\n  --text-secondary: #e0e0e0;\r\n  --text-tertiary: #bdbdbd;\r\n  \r\n  // Shadows (darker for dark theme)\r\n  --shadow-card: 0 2px 8px rgba(0, 0, 0, 0.4);\r\n  --shadow-card-hover: 0 4px 16px rgba(0, 0, 0, 0.6);\r\n  --shadow-overlay: 0 8px 32px rgba(0, 0, 0, 0.8);\r\n}\r\n\r\n// Theme transition\r\n* {\r\n  transition: background-color 0.3s ease, color 0.3s ease, border-color 0.3s ease;\r\n}\r\n\r\n// Prevent transitions on theme change for specific elements\r\n.no-theme-transition,\r\n.no-theme-transition * {\r\n  transition: none !important;\r\n}\r\n\r\n// Dark mode support (respects system preference when no theme is set)\r\n@media (prefers-color-scheme: dark) {\r\n  :root:not([data-theme]) {\r\n    // Apply dark theme variables when system prefers dark and no explicit theme is set\r\n    --color-primary: #4fc3f7;\r\n    --color-primary-dark: #29b6f6;\r\n    --color-secondary: #90a4ae;\r\n    --color-success: #4caf50;\r\n    --color-warning: #ff9800;\r\n    --color-error: #f44336;\r\n    \r\n    --bg-primary: #121212;\r\n    --bg-secondary: #1e1e1e;\r\n    --bg-tertiary: #2d2d2d;\r\n    \r\n    --text-primary: #ffffff;\r\n    --text-secondary: #e0e0e0;\r\n    --text-tertiary: #bdbdbd;\r\n    \r\n    --shadow-card: 0 2px 8px rgba(0, 0, 0, 0.4);\r\n    --shadow-card-hover: 0 4px 16px rgba(0, 0, 0, 0.6);\r\n    --shadow-overlay: 0 8px 32px rgba(0, 0, 0, 0.8);\r\n  }\r\n}\r\n"],"sourceRoot":""}]);
