import React, { useState, useRef, useCallback } from 'react';
import { useSetRecoilState, useRecoilValue } from 'recoil';
import { Text } from '@fluentui/react-text';
import { Card } from '@fluentui/react-card';
import { 
  currentCardIndexState, 
  selectedDishesState,
  restaurantState,
  isEditingCategoryState
} from '../../state/restaurantState';
import { Dish, SwipeDirection } from '../../types/restaurant';
import { getImageWithFallback, formatPrice } from '../../utils/restaurantLoader';
import styles from './SwipeCard.module.scss';

interface SwipeCardProps {
  dish: Dish;
  isActive: boolean;
  stackPosition: 'top' | 'middle' | 'back';
}

interface TouchState {
  startX: number;
  startY: number;
  currentX: number;
  currentY: number;
  isDragging: boolean;
}

const SwipeCard: React.FC<SwipeCardProps> = ({ dish, isActive, stackPosition }) => {
  const [touchState, setTouchState] = useState<TouchState>({
    startX: 0,
    startY: 0,
    currentX: 0,
    currentY: 0,
    isDragging: false,
  });
  const [showFullDescription, setShowFullDescription] = useState(false);
  const [isAnimating, setIsAnimating] = useState(false);
  
  const cardRef = useRef<HTMLDivElement>(null);
  const setCurrentCardIndex = useSetRecoilState(currentCardIndexState);
  const setSelectedDishes = useSetRecoilState(selectedDishesState);
  const restaurant = useRecoilValue(restaurantState);
  const selectedDishes = useRecoilValue(selectedDishesState);
  const isEditingCategory = useRecoilValue(isEditingCategoryState);

  const SWIPE_THRESHOLD = 100; // pixels
  const ROTATION_FACTOR = 0.1; // degrees per pixel

  const handleTouchStart = useCallback((e: React.TouchEvent) => {
    if (!isActive || isAnimating) return;
    
    const touch = e.touches[0];
    setTouchState({
      startX: touch.clientX,
      startY: touch.clientY,
      currentX: touch.clientX,
      currentY: touch.clientY,
      isDragging: true,
    });
  }, [isActive, isAnimating]);

  const handleTouchMove = useCallback((e: React.TouchEvent) => {
    if (!touchState.isDragging || !isActive || isAnimating) return;
    
    const touch = e.touches[0];
    setTouchState(prev => ({
      ...prev,
      currentX: touch.clientX,
      currentY: touch.clientY,
    }));
  }, [touchState.isDragging, isActive, isAnimating]);

  const handleTouchEnd = useCallback(() => {
    if (!touchState.isDragging || !isActive || isAnimating) return;
    
    const deltaX = touchState.currentX - touchState.startX;
    const deltaY = touchState.currentY - touchState.startY;
    const distance = Math.sqrt(deltaX * deltaX + deltaY * deltaY);
    
    // Check if it's a significant swipe
    if (Math.abs(deltaX) > SWIPE_THRESHOLD && Math.abs(deltaX) > Math.abs(deltaY)) {
      const direction: SwipeDirection = deltaX > 0 ? 'right' : 'left';
      handleSwipe(direction);
    } else {
      // Snap back to center
      setTouchState({
        startX: 0,
        startY: 0,
        currentX: 0,
        currentY: 0,
        isDragging: false,
      });
    }
  }, [touchState, isActive, isAnimating]);

  const handleSwipe = useCallback((direction: SwipeDirection) => {
    if (isAnimating) return;
    
    setIsAnimating(true);
    
    // Check if dish is currently selected
    const isDishSelected = selectedDishes.some(selectedDish => selectedDish.id === dish.id);
    
    if (direction === 'right') {
      // Add to selected dishes if swiped right (want) and not already selected
      if (!isDishSelected) {
        setSelectedDishes(prev => [...prev, dish]);
      }
    } else if (direction === 'left') {
      // Remove from selected dishes if swiped left (don't want) and currently selected
      if (isDishSelected) {
        setSelectedDishes(prev => prev.filter(selectedDish => selectedDish.id !== dish.id));
      }
    }
    
    // Animate card off screen
    const cardElement = cardRef.current;
    if (cardElement) {
      const flyDirection = direction === 'right' ? 1 : -1;
      cardElement.style.transform = `translateX(${flyDirection * 100}vw) rotate(${flyDirection * 30}deg)`;
      cardElement.style.opacity = '0';
    }
    
    // Move to next card after animation
    setTimeout(() => {
      setCurrentCardIndex(prev => prev + 1);
      setTouchState({
        startX: 0,
        startY: 0,
        currentX: 0,
        currentY: 0,
        isDragging: false,
      });
      setIsAnimating(false);
    }, 300);
  }, [dish, setCurrentCardIndex, setSelectedDishes, isAnimating, selectedDishes]);

  const toggleDescription = useCallback(() => {
    if (isActive) {
      setShowFullDescription(prev => !prev);
    }
  }, [isActive]);

  // Check if this dish is already selected
  const isDishSelected = selectedDishes.some(selectedDish => selectedDish.id === dish.id);
  const selectedCount = selectedDishes.filter(selectedDish => selectedDish.id === dish.id).length;

  // Calculate transform based on touch state
  const deltaX = touchState.currentX - touchState.startX;
  const deltaY = touchState.currentY - touchState.startY;
  const rotation = deltaX * ROTATION_FACTOR;
  const opacity = Math.max(0.7, 1 - Math.abs(deltaX) / 200);

  const cardStyle: React.CSSProperties = isActive && touchState.isDragging ? {
    transform: `translateX(${deltaX}px) translateY(${deltaY}px) rotate(${rotation}deg)`,
    opacity: opacity,
    transition: 'none',
  } : {
    transform: '',
    opacity: 1,
    transition: 'none',
  };

  return (
    <div
      ref={cardRef}
      className={`${styles.swipeCard} ${styles[stackPosition]} ${isActive ? styles.active : ''}`}
      style={cardStyle}
      onTouchStart={handleTouchStart}
      onTouchMove={handleTouchMove}
      onTouchEnd={handleTouchEnd}
    >
      <Card className={`${styles.card} ${isDishSelected ? styles.selected : ''}`}>
        <div className={styles.imageContainer}>
          <img 
            src={getImageWithFallback(dish.image, restaurant?.id)}
            alt={dish.name}
            className={styles.dishImage}
            onError={(e) => {
              const img = e.target as HTMLImageElement;
              // Prevent infinite error loops by only setting fallback once
              if (!img.src.includes('placeholder')) {
                img.src = getImageWithFallback('', restaurant?.id);
              }
            }}
          />
          <div className={styles.priceTag}>
            <Text size={400} weight="semibold" className={styles.price}>
              {formatPrice(dish.price)}
            </Text>
          </div>
          {isDishSelected && (
            <div className={styles.selectedIndicator}>
              <Text size={300} weight="semibold" className={styles.selectedText}>
                Selected {selectedCount > 1 ? `(${selectedCount})` : ''}
              </Text>
            </div>
          )}
        </div>
        
        <div className={styles.content}>
          <Text size={500} weight="semibold" className={styles.dishName}>
            {dish.name}
          </Text>
          
          <div className={styles.category}>
            <Text size={200} className={styles.categoryText}>
              {dish.category}
            </Text>
          </div>
          
          <div 
            className={styles.descriptionContainer}
            onClick={toggleDescription}
            role="button"
            tabIndex={isActive ? 0 : -1}
            aria-expanded={showFullDescription}
          >
            <Text 
              size={300} 
              className={`${styles.description} ${showFullDescription ? styles.expanded : ''}`}
            >
              {showFullDescription ? dish.fullDescription : dish.shortDescription}
            </Text>
              <Text size={200} className={styles.expandHint}>
                {showFullDescription ? 'Tap to collapse' : 'Tap for more details'}
              </Text>
          </div>
        </div>
      </Card>
    </div>
  );
};

export default SwipeCard;
